.copy-area #inviteCode.invite-page {
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 5px;
    margin-bottom: 10px;
    border-radius: 5px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.07);
}

.copy-area #inviteCode.invite-page .code-input {
    -webkit-align-self: center;
            align-self: center;
    font-size: 0.8rem;
    color: #333;
    -webkit-flex-grow: 2;
            flex-grow: 2;
    background-color: #fff;
    border: none;
}

.copy-area #inviteCode.invite-page .code-input:focus {
    outline: none;
}

.copy-area #inviteCode.invite-page #copy {
    width: 30px;
    height: 30px;
    margin-left: 20px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: #f8f8f8;
}

.copy-area #inviteCode.invite-page #copy i {
    display: block;
    line-height: 30px;
    position: relative;
}

.copy-area #inviteCode.invite-page #copy i::before {
    display: block;
    width: 15px;
    margin: 0 auto;
}

.copy-area #inviteCode.invite-page #copy.copied::after {
    position: absolute;
    top: 0px;
    right: 35px;
    height: 30px;
    line-height: 25px;
    display: block;
    content: "copied";
    font-size: 1em;
    padding: 2px 5px;
    color: #fff;
    background-color: #4099FF;
    border-radius: 3px;
    opacity: 1;
    will-change: opacity, transform;
    -webkit-animation: showcopied 1.5s ease;
    animation: showcopied 1.5s ease;
}

.copy-area #inviteCode.invite-page #copy:hover {
    cursor: pointer;
    background-color: #dfdfdf;
    transition: background-color 0.3s ease-in;
}

@-webkit-keyframes showcopied {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    70% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    100% {
        opacity: 0;
    }
}

@keyframes showcopied {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    70% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    100% {
        opacity: 0;
    }
}


