/* Details Styling
  ---------------------------- */
details,
summary {
    margin-bottom: 0 !important;
    /* for WordPress theme Blocksy */
}

details summary::-webkit-details-marker {
    display: none;
}

details summary {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    padding: 1rem 1.5rem;
    outline: none;
    cursor: pointer;
    font-size: 1.35rem;
    font-weight: 400;
    line-height: 1.2;
    border-bottom: 1px solid #000;
}

details[open] summary {
    background: #000;
    color: #fff;
}

details summary:before,
details summary:after {
}

details summary:before {
    content: ">";
    padding-right: 1.5rem;
}

details[open] summary:before {
    content: "^";
}

details[open] {
    border-bottom: 1px solid #000;
}

details>div {
    padding: 1.5rem 1.5rem .5rem;
    background: #f2f2f2;
}

details>div>* {
    margin-top: 0;
    margin-bottom: 1rem;
}


/* Accordion Details Img + Text elements
  ------------------------------*/

/* Reset previous styles */
details.accordion-details-img summary:before,
details.accordion-details-img[open] summary:before {
    display: none;
}

details.accordion-details-img summary {
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

details.accordion-details-img summary:after {
    content: "\f107";
}

details.accordion-details-img[open] summary:after {
    content: "\f106";
}

details.accordion-details-img>div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    overflow: auto;
}

details.accordion-details-img>div>a {
    width: 25%;
}

details.accordion-details-img>div>a>img {
    width: 100%;
}

details.accordion-details-img>div>div {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0;
    padding-left: 1.5rem;
}

details.accordion-details-img>div>div p {
    margin: 0 0 1rem 0;
}

@media (max-width:600px) {
    details summary {
        font-size: 1.5rem;
    }

    details.accordion-details-img>div>a {
        width: 100%;
    }

    details.accordion-details-img>div>div {
        padding: 0;
        padding-top: .5rem;
    }
}

